.cap-data-heading {
    grid-template-columns: 20% 50% 30% !important;
    p{
        font-size: 18px !important;
    }
    // :nth-child(3){
    //     width: 30% !important;
    // }
    // :nth-child(2){
    //     width:50% !important;
    // }
    

}
.cap-container{
    position: relative;
    .cap-loader{
        top: 50%;
    left: 45%;
    margin: 0;
    transform: translate(-50%, -50%);
    }
}

.cap-data-container{
    margin-top: -7px !important;
    gap: 3px !important;
    .cap-data{
        grid-template-columns:20% 50% 30% !important;
        p{
            font-size: 17px !important;
        }
       
        .team-name{
            padding: 6px 0px !important;
            justify-content: center !important;
        }
        :nth-child(3){
            font-size: 15px !important;
        }
    }
}
.cap-container{
    &.o{
        --orange-cap:#f77f00;
        >div:first-child{
        >div{
        border:  2px solid var(--orange-cap);
        >div{
            >h2{
                color: var(--orange-cap) !important;
                >svg{
                    fill: var(--orange-cap);
                }
            }   
        }

        >button>svg{
            stroke: var(--orange-cap);
        }
    }
}
>div:nth-child(2){
    background-color: var(--orange-cap);
}
.orange-data{
    >p{
        color: var(--orange-cap) !important;
        font-size: 19px;
        >svg{
            fill:var(--orange-cap) !important;
        }
    }
    
}
}
&.p{
    --purple-cap:#a06cd5;
    >div:nth-child(1){
    >div{
    border:  2px solid var(--purple-cap);
    >div{
        >h2{
            color: var(--purple-cap) !important;
            >svg{
                fill: var(--purple-cap);
            }
        }

    }
    >button>svg{
        stroke: var(--purple-cap);
    }

}
}
>div:nth-child(2){
    background-color: var(--purple-cap);
}
.purple-data{
    >p{
        color: var(--purple-cap) !important;
        font-size: 19px;
        >svg{
            fill:var(--purple-cap) !important;
        }
    }
    
}
}
&.mom{
    --mom-color:#8cb369;
    >div:first-child{
    >div{
        border:  2px solid var(--mom-color);
        >div{
            >h2{
                color: var(--mom-color) !important;
                >svg{
                    fill: var(--mom-color);
                }
            }
            >span{
                font-family: "Montserrat", sans-serif;
                color: var(--mom-color) !important;
            }
    
        }
        >button>svg{
            stroke: var(--mom-color);
        }
}
    }
    >div:nth-child(2){
        background-color: var(--mom-color);
    }
    >div:nth-child(3){
        >div:first-child{
        >p{
            color: var(--mom-color);
            font-weight: bolder;
            >svg{
                width: 45px;
                max-height: 100%;
                fill: var(--mom-color);
            }
        }
    }
    }
}
&.mot{
    --mot-color:rgb(255, 213, 0);
    >div:first-child{
    >div{
        border:  2px solid var(--mot-color);
        padding: 10px 0px ;
        >div{
            >h2{
                font-size: 21px;
                color: var(--mot-color) !important;
            
            }
            >span{
                
                color: var(--mot-color) !important;
            }
    
        }
        >button>svg{
            stroke: var(--mot-color);
        }
}
    }
    >div:nth-child(2){
        background-color: var(--mot-color);
    }
    >div:nth-child(3){
        >div:first-child{
        >p{
            color: var(--mot-color);
            >svg{
                width: 45px;
                max-height: 100%;
                fill: var(--mot-color);
            }
        }
    }
    }
}
>div:first-child{
   >div{
    background-color: transparent !important;

    >div{
        
    >h2{
        font-size: 28px;
        font-weight: 500;
        display: flex !important;
        align-items: center !important;
        gap: 8px;
        >svg{
            width: 45px ;
            height: 45px;
        }
    }
    >span{
        font-family: "Montserrat", sans-serif;
        font-weight: bolder;
        font-size: 11px;
    }
    }

    >button>svg{
        width: 32px ;
    height: 32px ;
    }
   }
}
}
.note-cover{
    padding: 5px 5px 10px;
    b{
        font-size: 15px;
    }
}

@media screen and (max-width:800px) {
    .cap-data-heading {
        p{
            font-size: 16px !important;
        }
        
    
    }
    .cap-container{
        position: relative;
        .cap-loader{
            top: 50%;
        left: 45%;
        margin: 0;
        transform: translate(-50%, -50%);
        }
    }
    
    .cap-data-container{
        margin-top: -4px !important;
        gap: 3px !important;
        .cap-data{
                >p{
                    font-size: 15px !important;
                    >svg{
                        width: 35px !important;
                        height: 28px;
                    }
                }
                
                
            grid-template-columns:20% 50% 30% !important;
            p{
                font-size: 14px !important;
            }
           
            .team-name{
                padding: 4px 0px !important;
            }
            :nth-child(3){
                font-size: 13px !important;
            }
        }
    }
    .cap-heading-container{
            div{
                gap: 3px;
                >div{
                    >h2{    
                        font-size: 22px !important;
                        gap: 4px;

                        &[name="mot"]{
                            font-size: 18px !important
                        }
                        >svg{
                            width: 35px !important;
                            height: 35px !important;
                        }

                    }
                    >span{
                        font-size: 9px !important;
                    }
                }

        }
       >div{
    
        >button>svg{
            width: 25px !important;
        height: 25px !important;
        }
       }
    }
    .note-cover{
        padding: 0px 4px 7px;
        p{
            font-size: 9px !important;
        }
        
        b{
            font-size: 11px;
        }
    }
}