.just-reset{
  position: static;
  transform: none;
  vertical-align: middle;
}
.reset{
  position: static;
  transform: none;
  fill: none;
  stroke: #264653;
  vertical-align: middle;
    width: 40px;
    height: 40px;
}
.reset-icon{
  position: static;
  transform: none;
  vertical-align: middle;
    width: 40px;
    height: 40px;
    fill: #264653;

}
   .back,.copy{
    position: static;
    transform: none;
    fill: none;
    stroke: #264653;
    stroke-width: 5px;
    width: 35px;
    height: 35px;
   }
   .back:hover,.copy:hover{
    fill:#264653 ;
    stroke:#f4a361 ;
   }
.Layer_1{
    position: static;
    transform: none;
    fill: none;
    width: 50px;
    height: 50px;
    stroke: #264653;
    border-right: 2px solid  #264653;
    flex-basis: 40%;
    justify-self: center;
  }

.ban{
  position: static;
  transform: none;
  fill: none;
  width: 35px;
  height: 35px;
    vertical-align: middle;
}
.random{
	position: static;
	transform: none;
	fill: none;
	height: 30px;
	width: 31px;
  stroke-width: 10px;
	stroke: #264653;
	vertical-align: middle;
  stroke-miterlimit:10
  }
  .friend{
    position: static;
	transform: none;
	fill: none;
	height: 30px;
	width: 30px;
	stroke: #264653;
	stroke-width: 20px;
	vertical-align: middle;
  }
  .copy{
    width: 26px;
    height: 26px;
    stroke-width: 12px;
    vertical-align: bottom;
  
  } 
  .leaderboard-svg{
    stroke-width: 10px;
    width: 32px;
    height: 30px;
  }
  .feedback-svg{
    fill: #264653;
  }
  @media screen and (max-width:800px){
    .Layer_1{
    width: 28px;
    height: 28px;
    flex-basis: 35%;
    }

    .ban{
     
      width: 21px;
      height: 21px;
    }
    .random{
      height: 30px;
      width: 32px;
    }
    .friend{
      height: 30px;
	width: 30px;
    }
    .back{
      width: 27px;
      height: 27px;
     }
     .copy:hover,.back:hover{
      fill:#264653 ;
      stroke:#f4a361 ;
     }
     .copy{
      height: 20px;
    width: 20px;
     }
     .leaderboard-svg{
      height: 30px;
    width: 30px;
      stroke-width: 8.5px;
    }
  }
