.loader-container1{
    display: flex;
    width: fit-content;
    border-radius: 50%;
    overflow:hidden;
    isolation: isolate;
}
.loader-container2{
    position: relative;
    transform-style: preserve-3d;
}

.loader-container2:before{
    content: "";
    position: absolute;
    left: 50%;
    width: 50px;
    height: 50px;
    translate: -50%;
    border-radius: 40px 25px 50px 60px;
    background-color: beige;
    animation: loader-up-down 2.5s linear infinite alternate;
}
.width-30px{
    width: max(1.961vw,23px);
    height: max(1.961vw,23px);
}
@keyframes loader-up-down {
    from{
        top: 100%;
        
        transform: rotate(0deg) translateZ(-1px);
    }
    to{
        top: -10%;
        transform: rotate(360deg) translateZ(-1px);
    }
}

.dot-loader {
    margin: 0px;
    width: 30px;
    aspect-ratio: 4;
    --_g: no-repeat radial-gradient(circle closest-side,beige 50%,transparent);
    background: 
      var(--_g) 0%   50%,
      var(--_g) 50%  50%,
      var(--_g) 100% 50%;
    background-size: calc(100%/3) 100%;
    animation: l7 1s infinite linear;
  }
  @keyframes l7 {
      33%{background-size:calc(100%/3) 0%  ,calc(100%/3) 100%,calc(100%/3) 100%}
      50%{background-size:calc(100%/3) 100%,calc(100%/3) 0%  ,calc(100%/3) 100%}
      66%{background-size:calc(100%/3) 100%,calc(100%/3) 100%,calc(100%/3) 0%  }
  }