div[ name='timer']{
    position: relative;
    width: 40px;
   aspect-ratio: 1;

    color: #86a146;
    background: beige;
    border: 3px solid #264653;
    border-radius: 50%;

    overflow: hidden;
}
div[ name='timer']>*{
    position: absolute;

translate: -50% -50%;
left: 50%;
transition: all 0.3s;
color: inherit;
}
.alert{
    color:#cc444b !important;
}
.warning{
    color:#fb8500 !important;
}


.middle-time{
    top: 50%;
    scale: 1;
    visibility: visible;
}
.top-time{
    top:5%;
    scale: 0.75;
    visibility: hidden;
}
.bottom-time{
    top:105%;
    scale: 0.75;
    visibility: hidden;
}

@media screen and (max-width:700px){
    div[ name='timer']{
        width: 30px;
    }
}