.lobby-container{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr;
    padding-block: 20px;
    row-gap:max(1.464vw,12px);
    column-gap:max(0.732vw,6px);
    background-size: cover;
    background-position: center;
}
.background-color-beige{
    background-color: beige;
}





.lobby-profile-container{
  overflow: hidden;
}
.lobby-profile-container>p{
    background: #264653;
    margin: 0px;
    color: beige;
    font-weight: lighter;
}
.lobby-profile-img{
    aspect-ratio: 1;
    width: 100px;
    background-size: cover;
  
}
.lobby-container>div:nth-child(3) .lobby-profile-img{
    background-image: url('../images/female/female1.png');
    opacity: 1;
    background-size: cover;
    transition: all 0.3s;
    animation: skeleton-loading 1s ease-out infinite ;
}

div.lobby-profile-img{
    background: #264653;
}


.lobby-timer-container{
    margin: 0px;
    display: grid;
    grid-template-rows:max-content 10% auto;
    grid-template-columns: 100%;
    justify-content: center;
    align-items: flex-start;
}

.lobby-timer-container>*{
    grid-row: 3/-1;
    margin: 0px;

    justify-self: center;
    align-self: center;
}
.lobby-timer-container>.coin{
   
    width: fit-content;
    height: calc(100% - 0.1vw);
}

.lobby-timer-container>*[name='timer'],.lobby-timer-container>span{
    grid-row: 1;
    
}

.lobby-timer-container>p:nth-child(1):nth-last-child(1){
    align-self: center;
    justify-self: center;
    width: fit-content;
}
.lobby-timer-container>span[name="game-room-id"]>span{
    padding: 3px;
    border-radius: 8px 0px 0px 8px;
}
.lobby-timer-container>span[name="game-room-id"]>button{
    width: 20%;
    padding: 3px;
    stroke: #264653;
    cursor: pointer;
    border-radius: 0px 8px 8px 0px;
    transition: background-color 1s;
}
.lobby-timer-container>span[name="game-room-id"]>button:hover{
    background-color: #264653;
    stroke: beige;
}


.double-rounded-button-container{
    height: 90%;
    justify-self: center;
    align-self: flex-end;
    width: min-content;
}

.rounded-button,.rounded-button>span{
    aspect-ratio: 1;
    border-radius: 50%;


}
.rounded-button{
    height: 90%;
    aspect-ratio: 1;
    justify-self: center;
    align-self: self-end;
    border: none;
    padding: 0px;
    background-color: #ff8e32;
    cursor: pointer;
}
.rounded-button>span{
    height: 100%;
    padding: 4px;
    background-image: url(./svg-src/toss.svg);
    border: 4px solid #264653;
    background-color: #f4a361;
    transform: translateY(-8px);
    transition: transform 0.2s ease-out;
}
.double-rounded-button-container>.rounded-button>span{
    width: 100%;
    padding: 6px;
}
.rounded-button:hover>span{
    transform: translateY(-9px);
}
.rounded-button:active>span{
    transform: translateY(-2px);
}
.heads-button,.heads-button>span{
    border-radius: 50% 0px 0px 50%;
}

.tails-button,.tails-button>span{
    border-radius: 0px 50% 50% 0px;
}
.opponent-status{

    position: absolute;
    left: 50%;
    translate: -50% 0%;
    max-width: 90%;
    width: max-content;
    top: -6%;
    border-radius: 12px;
    text-align: center;
    background: beige;
    color: #264653;
    font-size: 10px;
    padding: 6px 12px;
    box-shadow: 0px 2px 8px 2px #6c757d;
}
.cancel-matchmaking{

    grid-row: 2;
    grid-column: 1/2;
    justify-self: self-start;

    padding: 0px;
    margin: 0px 0px 0px 18px;
    border: none;
    outline: none;
    background:none;

    transition: all 0.3s;
    cursor: pointer;
}
.cancel-matchmaking>svg{
    width: fit-content;
    max-width: max(2.562vw,28px);
    stroke: #264653;
    background-color: beige;
    padding: 1px;
    scale: 1.1;
    border-radius: 8px;
    transition: all 0.2s;
}
.cancel-matchmaking:hover>svg{
    padding: 2px;
    scale: 1.2;
    stroke: beige;
    
    background-color:#264653 ;
}


.lobby-status{
    grid-row: 2;
    grid-column: 1 / -1;
    border-radius: max(0.586vw, 5px);
    color: #264653;
    padding: max(0.439vw, 4px) max(0.878vw, 7px);
}
@keyframes skeleton-loading {
    0%{  
       position: relative;
       left: 0%;
       scale: 1;
       opacity: 1;
    }
    50%{
        position: relative;
        left: 120%;
       opacity: 1;
       scale: 0.7;
    }
    50.1%{
        opacity: 0;
    }
    50.2%{
        position: relative;
        left: -100%;
        opacity: 0;
    }
    100%{
        position: relative;
        left: 0%;
        opacity: 1;
        scale: 1;
    }
}

@media screen and (max-width:700px){
    .lobby-profile-container{
        margin-block:max(0.735vw,7px);;
    }
    .lobby-profile-img{
        width: 70px;
    }
    .double-rounded-button-container{
        height: 75%;
    }
    
    .rounded-button>span{
        padding: 3px;
        border-width: 3px;
        transform: translateY(-6px);
    }
}

