#container{
   height: 100%;
    position: absolute;
    top: 0;

    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin: 0px auto;
    overflow: hidden;
    z-index: 2;
}



.top-container{
   width: 60%;
   display: grid;
   grid-template-columns: 30% 40% 30%;
   grid-template-rows: auto auto;
   align-items: center;
   row-gap: max(0.366vw,3px);
   margin: 30px auto 0px;
 }

 .main{
   display: grid;
   grid-template-rows: auto auto auto;
   grid-template-columns: 2.5fr 1fr;
  row-gap: max(0.366vw,3px);
  column-gap: max(0.732vw,6px);
   height: 100%;
   margin-inline:max(0.735vw,7px);
 } 
 .main>*{
   border-radius: 4px;

   background-position: center;
   background-size: cover;
 }
 .main>div:nth-of-type(1){
   display: grid;
   grid-template-columns: 1fr 1fr;
   gap: max(0.366vw,3px);
  
 }
 .main>div:nth-of-type(1)>p:nth-child(1){
  grid-column: 1/-1;
 }
 .main>div:nth-of-type(1)>p{
  background-color: beige;
  padding: max(0.146vw,1px);
  border-radius: 4px;
 
}
 .top-container>div:nth-child(4){
   grid-row: 2;
   grid-column: 1 / -1;
   justify-self: center;
   align-items: center;
 }
 .top-container>div:nth-child(4)>p{
  display: flex;
  justify-content: center;
  align-items: center;
  height: max(2.928vw, 30px);
  aspect-ratio: 1;
  font-weight: bold;
  padding: max(0.293vw, 2px) max(0.586vw, 4px);
  border: max(0.293vw, 2px) solid;
  border-radius: 50%;
  background-color: beige
 }

 .main>span, .main>p{

   grid-column: 1/-1;
   background-color: #264653;
   color: wheat;

   font-family: "Montserrat", sans-serif;
 }

 .run{
   color: white;

 }
 .zero{
   background-color: #cc444b;
 }
 .one{
   background-color: #979dac;
 }
 .two{
   background-color: #d4d700;
  }
 .three{
   background-color:#61a5c2;
 }
 
 .four{
   background-color:#a7c957;
  }
  .five{
   background-color:#ffc300;
  }
  .six{
   background-color:  #fb8500;
  }


  .button:has(.one),.one-color{
   color: #797e8a;
   fill: #797e8a;
 }
 .button:has(.three),.three-color{
   color: #4e849b;
   fill: #4e849b;
 }
 .button:has(.four),.four-color{
   color: #86a146;
   fill: #86a146;
 }
 .button:has(.six),.six-color{
   color: #c96a00;
   fill: #c96a00;
 }
 .button:has(.five),.five-color{
   color: #cc9c00;
   fill: #cc9c00;
 }
 .button:has(.two),.two-color{
   color: #aaac00;
   fill: #aaac00;
 }

 .score{
   background-image: url('./svg-src/hand-cricket-common-background.svg');
   background-position: center;
   background-size: cover;
   border-radius: 16px;
 }

 .img-profile{
  width: 25%;
  border-radius: 100%;
  scale: 1.5;
  aspect-ratio: 1;
  transform-origin: 50% 80%;
  border: 3px solid #cee6a3;
}

.bottom-container{
   display: flex;
   flex-direction: column;
   margin: 0px auto;
   padding: 15px 0px;
   gap: 10px;
   width: 100%;
}




@keyframes fadeInAnimation {
    0% {
      transform: translateY(-200px);
      opacity: 0%;
    }
   
    100%{
      transform: translate(25.8px,-140px);
      opacity: 100%;
    }
   
}


.run-button-container{
   position: relative;
   display:grid;
   justify-content: center;
   grid-template-columns:repeat(6,1fr);
   gap: max(2.083vw,20px);
   padding:max(2.196vw,21px) max(1.464vw,14px) max(1.464vw,14px);
   border-radius: 8px;
   background: #00000033;
   box-shadow: 0px 2px 8px 0px #80808096;
}

.run-button-container *{
   border-radius: 50%;
   background-color: beige !important;
   color: inherit;
}

.run-button-container>button>span{
   display: flex;
   width: 100%;
   justify-content: center;
   align-items: center;
   width: max(7.54vw,90px);
   aspect-ratio: 1.56;
   padding: 0px;
   border: 4px solid;
}
.run-button-container:has(svg)>button>span>svg{
   height: 65%;
   width: 65%;

}
.run-button-container>button{
 
    outline-width: 3px;
    outline-style:solid;
    outline-color: inherit;
}

.match-result{
   margin-bottom: max(0.368vw, 3px);
}
.match-result+p+button{
  margin-top: max(1.103vw,12px);
}
.match-result+div>label{
   line-height: 1.5;
}
.player-status-container{
 
   width: fit-content;
   display: flex;
   background-color: beige;
   border: 3px solid  #264653;
   justify-content: center;
   align-items: center;
   border-radius: 8px;
   padding:max(0.586vw,5px);
 }
 .player-status-container>svg{
   width: 70px;
   padding: 0px max(0.366vw,3px) 0px 0px;
 }
 .player-status-container>span{
   padding-left: 5px;
    border-left: 3px solid;
 }


