
.coin{
    --my-start-deg: 0deg;
    --my-end-deg: 7920deg;
    aspect-ratio: 1;
    height: 100%;
    margin: 0px;
    position: relative;
    display: inline-block;
    -webkit-transform-style:preserve-3d;
    transform-style: preserve-3d;
    animation:animate ease-out 6s forwards;

    
}

.heads,.tails{
    left: 0;
    top:0;
    transform: translate(0);
    width: 100%;
    line-height: 100%;
    vertical-align: middle;
    height: 100%;
    position: absolute;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
}
.tails{
    transform: rotateX(180deg);
}
@keyframes animate {
    0%{
        transform: rotateX(var(--my-start-deg));
    }
    100%{
        transform: rotateX(var(--my-end-deg));
    }
}


@media screen and (max-width:800px){
    .visible{
        margin-bottom: 0px;
    }
    .coin{
        width: 100px;
        height: 100px;
        margin: 7px;
    
        
    }
}