.tournament-container{
    max-width: 100%;
    height: 75vh;
    background-color:#264653 ; 
    padding: 10px; 
    display: flex;
    position: relative;
    flex-direction: column;
    justify-content: flex-start;
    gap: 10px;
    border-radius: 10px;
    >div{
        margin: 0;
    }
    .Leaderboard-data-container{
        position: relative;
        margin-top: -5px;
        
    }
    .selection-div{
        padding: 5px; 
        >select{
            margin: 0 !important;

        }
    }
    .loader{
        top: 22%;
    left: 45%;
        margin: 0;
        transform: translate(-50%, -50%);
    }
    .tournament-heading-container-1{
        display: flex;
        background-color: #f4a361;
        justify-content: space-between;
        gap:5px;
        padding: 5px;
        border-radius: 9px;
        h2>svg{
            width: 50px;
            height: 50px;
        }
        button{
            position: static;
            margin: 0px 0px 0px 5px;

        }
       
    }

    .country-cup-data-heading{
        display: grid;
        width: 100%;
        border-radius: 5px;
        background-color: #f4a361;
        grid-template-columns: 8% 54% 7% 7% 7% 7% 10%;
        grid-template-rows: fit-content;


        p{
           font-size: 16px; 
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 6px;
            border: 1px solid #264653;
            height: 100%;
            width: 100%;
        }
        p:nth-child(1){
            font-size: 14px;
        }
        p:last-child{
            font-size: 12px;
            >span{
                font-size: 11px;
            }
        }
        
    }
    .country-cup-data{
        display: grid;
        width: 100%;
        border-radius: 5px;
        background-color: #f4a361;
        grid-template-columns: 8% 54% 7% 7% 7% 7% 10%;
        grid-template-rows: fit-content;
        background-color:#d9d9d922 ;
        p{
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            height: 100%;
            border: 1px solid #264653;
            font-size: 14px;
            color: #c9cba3;
        }
        span{
            font-size: 10px;
            margin: 0px;
            color: rgb(201, 203, 163)
        }

        .team-name{
            font-family: 'Montserrat', sans-serif;
            font-weight: bolder;
            padding: 10px 0px;
            text-align: center;
            display: flex;
            justify-content: flex-start;
            gap: 13px;
            font-size: 15px;
            align-items: center;
            img{
                display: inline-block;
                transform-origin: center;
                margin-left: 13px;
            }
        }
    }
    .overall-stats-data{
        grid-template-columns: 70% 20% !important;
        padding: 5px;
        >p{
            font-family: 'Montserrat', sans-serif;
            font-weight: bolder;
            border: none;
            >svg{
                fill:#f4a361;
                margin: 0;
                height: 40px;
                .st30day0{
                    fill:#f4a361;
                }
            }
        }
    }
    .player-stats-data{
        grid-template-columns: 23% 19.25% 19.25% 19.25% 19.25% !important;
        padding:5px ;
        >p{
            font-family: 'Montserrat', sans-serif;
            font-weight: bolder;
            border: none;
            >svg{
               
                margin: 0;
                height: 35px;
            }
           
    }
}
    img{
        width: 24px;
        margin: 0;
    }
    .country-cup-radio-button{
        display: flex;
        justify-content: space-around;
        >div{
            input{
                display: none
            }
            input[type="radio"]:checked+label{
                background-color: #264653;
                color: #f4a361;
                border:1px solid #f4a361;
                svg{
                    fill: #f4a361;
                    stroke: #f4a361;
                }
            }
        }
        >div>label{
            font-size: 14px;
            display: flex;
            justify-content: center;
            padding: 3px;
            gap: 5px;
            align-items: center;
            background: none;
            border: none;
            background-color: #f4a361;
            border-radius: 5px;
            border:1px solid #264653;
            transition: all 0.3s;
    
             
        }
    }
    .tournament-middle-container-1, .tournament-middle-container-2{
        display: flex;
        margin-top: 5px;
        gap:10px
    }
    .tournament-middle-container-2{
        width: 98%;
        margin-top: 0px;
    }
    .play-off-info{
        background-color: rgba(217, 217, 217, 0.1333333333);
        display: flex;
        flex-direction: column;
        width: fit-content;
        padding: 5px; 
        border-radius: 5px;
        >p{
            font-size: 12px;
            color: #f4a361;
            border-bottom: 2px solid #264653;
            padding: 5px 0px;
            width: 100%;
        }
        >div.team-versus-cover{
            display: flex;
            flex-direction: row;
   
            >div.team-versus-container{
                display: grid;
                grid-template-columns: repeat(3,min-content);
                justify-content: center;
                align-items: center;
                column-gap: 10px;
                row-gap: 5px;
                padding: 5px 10px;
                p{
                    color: #c9cba3;      
                }
                p:last-child{
                    grid-column: 1/-1;
                }
                .single-team-versus{
                    display: flex;
                    gap:8px;
                    align-items: center;
                    img{
                        height: 100%;
                    }
                    >div>p:nth-child(1){
                        font-size: 15px;
                    }
                    >div>p:nth-child(2){
                        font-size: 11px;
                    }
                }
                
            }
            >div.team-versus-container:nth-child(2){
                border-left: 2px solid #264653 ;
            } 
        }

    }
}
.qualified{
    color: #dbb42c !important;
}
.eliminated{
    color: #da7179 !important; 
}

@media screen and (max-width:800px){

    .tournament-container{
        padding: 5px; 
        gap: 7px;
        .tournament-middle-container-1, .tournament-middle-container-2{
            
            margin-top: 3px;
            gap:7px
        }
        .tournament-middle-container-2{
            width: 99%;
        }
        .selection-div{
            >select{ 
            &#contribute{
               font-size: 12px !important;
    
            }
        }
        }
      
        .tournament-heading-container-1{
            padding: 3px;
                >div{
                    >h2{
                        font-size: 25px;
                    }
                    >h2>svg{
                       
                        width: 40px;
                        height: 40px;
                    }
            }

           
        }

        .country-cup-data-heading{
            border-radius: 3px;
            grid-template-columns: 9% 48% 8% 8% 8% 8% 11%;
    
            p{
               font-size: 12px; 
                padding: 3px;
            }
            p:nth-child(1){
                font-size: 12px;
            }
            p:last-child{
                font-size: 11px;
                >span{
                    font-size: 9px;
                }
            }
            
        }
        .country-cup-data{
            border-radius: 3px;
            grid-template-columns: 9% 48% 8% 8% 8% 8% 11%;
            p{
            
                font-size: 10px;
            }
            p:last-child{
                font-size: 11px;
                >span{
                    font-size: 8px;
                }
              
            }
            .team-name{
                padding: 7px 0px;
                gap: 5px;
                font-size: 10px;
               
                img{
                    width: 20px;
                    scale: 1.1;
                    margin-left: 5px;
                }
            }
        }
        
        .overall-stats-data{
            >p{
                >svg{
                    height: 30px;
                }
            }
        }
        .player-stats-data{
            padding:5px ;
            >p{
                >svg{
                    height: 35px;
                    height: 22px;
                    max-width: 30px;
                }
               
        }
    }
        img{
            width: 20px;
        }
        .country-cup-radio-button{
            >div>label{
                padding: 5px;
                gap: 5px;
                font-size: 10px;
                >svg{
                    width: 21px;
                    height: 21px;
                }
            }
        }
        .play-off-info{
            >p{
                font-size: 12px !important;
            }
            .single-team-versus{
                >div>p:nth-child(1){
                    font-size: 11px !important;
                }
                >div>p:nth-child(2){
                    font-size: 9px !important;
                }
            }
        }
    }

}




.button-54 {
    font-family: "Open Sans", sans-serif;
    letter-spacing: 2px;
    text-decoration: none;
    text-transform: uppercase;
    color:#f77f00;
    cursor: pointer;
    border: 3px solid;
    padding: 0.25em 0.5em;
    box-shadow: 1px 1px 0px 0px, 2px 2px 0px 0px, 3px 3px 0px 0px, 4px 4px 0px 0px, 5px 5px 0px 0px;
    position: relative;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    background-color: #3e5a65;
    border-radius: 5px;
    flex-basis: 100%;
    span{
        color: inherit;
        margin: 0px;
        padding: 0px;
        
    }
    >span{
        display: flex;
        justify-content: flex-start;
        align-items: center;
        gap: 3px;
        color: #f77f00;
        font-size: 11px;
        >span{
            >svg{
                fill: #f77f00;
                width: 35px;
                height: 35px;
            }

        }
        >span:last-child{
            >span:first-child{
                font-family: "Montserrat", sans-serif;
                font-weight: bolder;
            }
        }
    }
    &.p{
        color: #a06cd5;
        >span{
            color: #a06cd5;
            >svg{
                fill: #a06cd5;
            }
        }
    
      }
      &.mot{
        color: rgb(255, 213, 0);
        >span{
            color: rgb(255, 213, 0);
            >svg{
                fill:  rgb(255, 213, 0);
            }
        }
      }

  }
  .button-54:not([disabled]):active{
    box-shadow: 0px 0px 0px 0px;
    top: 5px;
    left: 5px;
  }
  
  @media  screen  and (max-width:800px)  {

    .button-54 {
        letter-spacing: 1px;
        border: 2px solid;
        padding: 3px 3px;
        border-radius: 4px;
        box-shadow:  0px 0px 0px, 1px 1px 0px 0px, 2px 2px 0px 0px, 3px 3px 0px 0px, 4px 4px 0px 0px;
      
        >span{
            font-size: 8px !important;
            margin: 0px !important;
            >span{
                >svg{
                    width: 25px !important;
                    height: 25px !important;
                }
                :last-child{
                    display: flex;
                    flex-direction: column;
                }
            }

        }
    
      }
  }
