.fixture-one-data-wrapper{
    height: 100%;
    overflow: hidden;
    padding-block: 10px;
    overflow-y: scroll;
    display: flex;
    flex-direction: column;
    gap: 15px;
}
.fixture-one-data-container{
  position: relative;
    width: 100%;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    background-color: rgba(217, 217, 217, 0.1333333333);
}
.fixture-one-data-container *{
    color: #f4a361;
    margin: 0px;
}
.fixture-one-data-container>div:nth-child(1),.fixture-one-data-container>div:nth-child(3){
  display: grid;
  grid-template-columns: 50% 50%;
}
.fixture-one-data-container>div:nth-child(1)>span,.fixture-one-data-container>div:nth-child(3)>span{
  padding: 10px 5px;
}
.fixture-one-data-container>div:nth-child(1)>span:nth-child(2),.fixture-one-data-container>div:nth-child(3)>span:nth-child(2){
  border-left: 2px solid #264653;
}
.fixture-one-data-container>div:nth-child(2){
    display: grid;
    grid-template-columns: 45% 10% 45%;
    justify-items: center;
    align-items: center;
    border-block: 2px solid #264653;
    padding: 10px 5px;
}

.fixture-one-data{
  display: flex;
  align-items: center;
  gap: 5px;
}
.fixture-one-data *{
    color: #c9cba3 !important;
    
  }
.fixture-one-data>img{
    width: 30px;
    height: 100%;
}

.fixture-one-data>div{
    display: flex;
    flex-direction: column;
    gap: 3px;
}
.match-status{
  position: absolute;
  background: #f4a361;
  color: #3e5a65;
  padding: 2px;
  border-radius: 4px;
  top: 0;
  right: 50%;
  transform: translate(50%,-50%);
}

  .fixture-one-data p{
    font-size: 14px;
  }
  .fixture-one-data-container>div:nth-child(3)>span, .fixture-one-data span,.fixture-one-data-container>div:nth-child(3)>span>span{
    font-family: "Montserrat", sans-serif;
  }
  .fixture-one-data span,.fixture-one-data-container>div:nth-child(3)>span>span,.match-status{
    font-size: 11px;
}

@media screen and (max-width:800px){
  .fixture-one-data-container>div:nth-child(1)>span, .fixture-one-data-container>div:nth-child(3)>span,.fixture-one-data p {
    font-size: 12px;
}
.fixture-one-data span, .fixture-one-data-container>div:nth-child(3)>span>span, .match-status {
  font-size: 10px;
}
.fixture-one-data>div {
  gap: 1px;
}
.fixture-one-data>img {
  width: 24px;
}
}