.Leaderboard{
    position:relative;
    display: flex;
    flex-direction: column;
    gap: 5px;
    width: 95%;
    padding: 10px;
    border-radius: 10px;
    height: 78vh;
    background-color: #264653;
 
}
.Leaderboard-main-heading{
    display: flex;
    width: 100%;
    justify-content: space-around;
    background-color: #f4a361;
    border-radius: 8px;
    h2{
        margin: 0; 
    padding: 7px 7px 7px 0px;
    font-weight:500;
    width: 75%;
    display: flex;
    align-items: center;
    gap: max(0.735vw,7px);
    >svg{
        width: 45px;
        height:45px;
    }
    }
    
    button{
        display: flex;
        justify-content: center;
        align-content: center;
        flex-basis: fit-content;
    }
    .back-button{
        position: static;
        margin: 0;
        
    }
}
.Leaderboard-heading-container h4{
    background-color: #f4a361;
    padding: 5px;
    border-radius: 5px;
    font-weight:600;
}
.Leaderboard-heading-container{
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 10px;
    flex-basis: 1;
    margin: 0;
}
.Leaderboard-headings{
    display: grid;
    width: 100%;
    grid-template-columns: 20% 56% 24%;
    border-radius: 5px;
}
.Leaderboard-headings p{
    font-size: 20px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 8px 4px;
    background-color: #f4a361;
   &:first-child,&:nth-child(2){
    border-right: 2px solid #264653;
   }
   &:first-child{
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
   }
   &:last-child{
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
   }
}
.timeline-buttons{
    display: flex;
    justify-content: space-between;
    width: 100%;
    gap: 5px;    
}
.timeline-buttons-button{
    cursor: pointer;
    background-color: #f4a361;
    display: grid;
    width: 33%;
    gap: 0;
    height: fit-content;
    grid-template-columns: repeat(10,10%);
    grid-template-rows: repeat(12,5px);
    transition: all 0.7s ease;
    padding: 0;
    overflow: hidden;
    border: none;
    border-radius: 5px;
    >span:first-child{
        grid-column: 1/8;
        grid-row: 1/9;
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        border-top: 2px solid #e76f51;
        border-left: 2px solid #e76f51;
        border-top-left-radius: 5px;
    
    }
  
    .championship{
        font-weight: 100;
        font-size: 10px;
        
    }
    .ends{
        font-size: 12px;
        display: flex;
        justify-content: center;
        align-items: center;
        grid-column: 1/-1;
        width: 100%;
        height: 100%;
        grid-row: 9/-1;
        background-color: #e76f51;
    
        
    }
    svg{
        background-color: #e76f51;
        grid-row: 1/9;
        grid-column: 8/-1;

    }
   
}
.goat{
    p{
        grid-column: 1/8;
        grid-row: 1/9;
    
    }
}
.timeline-buttons-button:hover,.timeline-buttons-button.active{
    background-color: #264653;
    p,.championship{
        color:#f4a361;
    }
    
}
.loader{
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top:50px;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    border: 4px solid #264653; /* Light grey */
    border-top: 4px solid #e76f51; /* Blue */
    animation: spin 2s infinite;
}
@keyframes spin {
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
    
}
.Leaderboard-winner{
    background-color: rgb(108 126 133 / 51%);
    padding: 6px 12px;
    border-radius: 4px;
    display: flex;
    justify-content: center;
    align-items: center;
    span{
        color: #f4a361;
        >span{
            font-family: "Montserrat", sans-serif;
            font-weight: bolder;
            margin-right: 5px;
        }
    }
    svg{
        padding: 2px;
    }


}
.Leaderboard-data-container{
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 5px;
    flex-basis: 1;
    margin: 0;
    overflow-y: auto;
    scrollbar-width:none;
    
}
.Leaderboard-data{
    width: 100%;
    display: grid;
    grid-template-columns: 20% 56% 24%;
    grid-template-rows: fit-content;
    border-radius: 5px;
    background-color:rgba(217, 217, 217, 0.1333333333);
    &:first-child{
        background-color: #fef6ef52;
        svg{
            padding: 2px;
            width: 35px;
            height: 35px;
        }
        p{
            font-size: 20px;
            color: #f4a361;
            font-weight: bolder;
            padding: 3px !important;
        }
    }
    &:nth-child(2){
        background-color: #fef6ef41;
        p{
            font-size: 19px;
            color: #85afc9;
            font-weight: bolder;
        }
    }
        &:nth-child(3){
            background-color: rgba(254, 246, 239, 0.190);
        p{
            font-size: 18px;
            color: #9ac1ae;
            font-weight: bolder;
        }
    }
    p{
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 17px;
        padding: 8px 4px;
        border: 1px solid #264653;
        color: #c9cba3;

        &:nth-child(2){
            font-family: "Montserrat", sans-serif;
        }
    }
}
.Leaderboard-goat-data{
    width: 100%;
    display: grid;
    grid-template-columns: 60% 20% 20%;
    align-items: center;
    padding: 8px 4px 8px 8px;
    background-color:rgba(217, 217, 217, 0.1333333333);
    border-radius: 4px;
    p{
        color: #c9cba3;
        margin: 0px;
        font-family: "Montserrat", sans-serif ;
        font-size: 16px;
        &:first-child{
            justify-self: flex-start;
            align-self: center;
         
        }
        svg{
            width: 35px;
            height: 35px;
        }
    }
    &:first-child{
        background-color: #fef6ef52;

        p{
            font-size: 19px;
            color: #f4a361;
            font-weight: bolder;
        }
    }
    &:nth-child(2){
        background-color: #fef6ef41;
        p{
            font-size: 18px;
            color: #85afc9;
            font-weight: bolder;
        }
    }
        &:nth-child(3){
            background-color: rgba(254, 246, 239, 0.190);
        p{
            font-size: 17px;
            color: #9ac1ae;
            font-weight: bolder;
        }
    }
}

@media screen and (max-width:800px){
    .Leaderboard{
        gap: 3px;
     
    }
    .Leaderboard-headings p{
        font-size: 14px;
        padding: 6px 3px;
    }
    .Leaderboard-data-container{
        gap: 3px;
    }
    
    .Leaderboard-data{
        border-radius: 5px;
        &:first-child{
            svg{
                padding: 1px;
                width: 27px;
                height: 27px;
            }
            p{
                font-size: 15px;
                padding: 2px !important;
            }
        }   
        &:nth-child(2){
            p{
                font-size: 14px;
            }
        }
            &:nth-child(3){
            p{
                font-size: 13px;
            }
        }
        p{
            font-size: 12px;
            padding: 6px 3px;
    
        }
    }
    .Leaderboard-goat-data{
        padding: 6px 4px 6px 6px;
        p{
            font-size: 12px;
            svg{
                width: fit-content;
                height: 27px;
            }
        }
        &:first-child{
    
            p{
                font-size: 15px;
            }
        }
        &:nth-child(2){
            p{
                font-size: 14px;
            }
        }
            &:nth-child(3){
            p{
                font-size: 13px;
            }
        }
    }
    
    .Leaderboard-main-heading{
        gap: 0px;
        
        button{
            svg{
                width: 27px;
            }
        }
        h2{
            padding: 4px;
            font-size: 25px;
            width: 80%;
            >svg{
                width: 30px;
                height: 30px;
            }
        }
       

    }
    
    .timeline-buttons-button
{
    grid-template-rows: repeat(12,3.7px);
        p{
            font-size: 11px;
        }
        .championship{
            font-size: 7px;
            
        }
        .ends{
            font-size: 8px;      
        }
        svg{
            height: 100%;
            width: 100%;
        }
    } 
    .Leaderboard-winner{
        padding:4px 8px;
        border-radius: 4px;
        font-size: 10.5px;
        svg{
            height:29px;
            width: 29px;
        }
    }
    }